}
.icons li {
  display: inline;
  padding: 0 5px;
}
.category h2 {
  color: #000;
  font-size: 20px;
  display: block;
  margin: 15px auto 15px;
  padding: 0 15px 0 15px;
  border-bottom: 2px solid;
}
.category h2::before {
  display: inline-block;
  width: 5px;
  height: 20px;
  background-color: #000;
